body {
	min-width: 320px;
	color: $white;
	background: $bodyBg;
	@include f('ll', 16);
	@include font-smoothing;
	line-height: 1.4; }

.out {
	overflow: hidden;
 }	// background: url(../img/hero-bg.png) no-repeat top center

.container {
	@include clr; }

.inner {
	position: relative;
	padding: 0 10px;
	margin: 0 auto;
	max-width: 1140px;
	@include clr;
	@include md {
		max-width: 920px; }
	@include sm {
		max-width: 590px; } }

.slider-loaded {
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease, visibility 0.3s ease;
	&.is-loaded {
		opacity: 1;
		visibility: visible; } }

h1, h2, h3, h4, h5, h6, strong, em, i, cite, th {
	font-weight: normal;
	font-style: normal; }

a {
	text-decoration: none;
	color: inherit; }
