.team {
	position: relative;
	z-index: 10;
	padding-bottom: 70px;
	@include xs {
		padding-bottom: 35px; }
	&__content {
		margin: 0 -10px;
		@include clr;
		@include sm {
			margin: 0; } }
	&__item {
		float: left;
		padding: 0 10px;
		width: 50%;
		@include sm {
			float: none;
			padding: 35px 0;
			margin: 0 auto;
			width: 100%;
			max-width: 500px; } }
	&__heading {
		margin-bottom: 75px;
		@include md {
			margin-bottom: 100px; }
		@include xs {
			margin-bottom: 65px; } } }

.member {
	position: relative;
	padding-left: 190px;
	@include xs {
		padding-left: 0; }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		border-radius: 50%;
		width: 170px;
		height: 170px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		box-shadow: 0 0 68px rgba(0, 246, 255, 0.1);
		@include xs {
			position: relative;
			float: left;
			width: 100px;
			height: 100px; } }
	&__content {
		max-width: 320px;
		@include md {
			max-width: 245px; }
		@include sm {
			max-width: 320px; }
		@include xs {
			max-width: 100%; } }
	&__name,
	&__position {
		@include xs {
			padding-left: 120px; } }
	&__name {
		margin-bottom: 10px;
		@include f('black', 22, 30);
		@include xs {
			padding-top: 10px;
			font-size: 18px; } }
	&__position {
		margin-bottom: 20px;
		@include f('ll', 20, 30);
		@include xs {
			margin-bottom: 0;
			font-size: 16px; } }
	&__description {
		@include f('ll', 16, 30);
		@include xs {
			clear: both;
			padding: 20px 0 0;
			@include f(null, 14, 24); } } }
