.modal {
	padding: 87px 0 74px;
	font-size: 18px;
	border-top: 1px solid rgba($white, 0.06);
	border-bottom: 1px solid rgba($white, 0.06);
	display: none;
	@include clr;
	@include sm {
		padding: 57px 0 72px; }
	&__left {
		float: left;
		padding-right: 50px;
		width: 476px;
		@include sm {
			float: none;
			padding-right: 0;
			margin: 0 auto;
			width: 428px; }
		@include xs {
			width: 300px; }
		&--desktop {
			@include sm {
				display: none; } }
		&--mobile {
			display: none;
			@include sm {
				display: block; } } }
	&__right {
		float: right;
		width: calc(100% - 476px);
		text-align: left;
		@include sm {
			float: none;
			margin-top: 97px;
			width: 100%; }
		.btn {
			margin-top: 38px; } }
	&__right--w100 {
		width: 100%; }
	&__title {
		position: relative;
		margin: 0 12px 26px 0;
		padding: 7px 20px 7px 30px;
		color: $black;
		@include f('black', 30);
		line-height: 1.2;
		text-transform: uppercase;
		vertical-align: top;
		background: $yellow;
		display: inline-block;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 100%;
			@include arr(12, 50, $yellow, tl);
			@include md {
				display: none; } }
		@include md {
			padding: 7px 20px; } } }
.characteristics {
	margin-top: 35px;
	&__row {
		@include clr;
		&:not(:first-child) {
			margin-top: 12px; } }
	&__name {
		float: left;
		width: 175px;
		@include f('black'); }
	&__data {
		float: right;
		width: calc(100% - 175px);
		@include clr; }
	&__color {
		float: left;
		width: 26px;
		height: 24px;
		&:not(:first-child) {
			margin-left: 9px;
			@include md {
				margin-left: 0; } }
		@include xs {
			margin: 5px 5px 0 0; } } }

.slider-for,
.slider-nav {
	overflow: hidden;
	transition: opacity .2s ease-in-out;
	transition-delay: .5s;
	&.is-loaded {
		opacity: 1; }
	&__slide {
		width: 33%;
		&:not(:first-child) {
			display: none;
			.is-loaded & {
				display: block; } }
		img {
			margin: 0 auto;
			display: block; } } }
.slider-nav-out {
	overflow: hidden; }
.slider-nav-wrap {
	position: relative;
	margin-top: 15px;
	opacity: 0;
	transition: opacity .2s ease-in-out;
	transition-delay: .5s;
	@include xs {
		margin: 25px auto 0;
		width: 250px; }
	&.is-loaded {
			opacity: 1; }
	&:before,
	&:after {
		position: absolute;
		content: "";
		top: -9px;
		bottom: -9px;
		width: 1px;
		background: #5B6772; }
	&:before {
		left: 0; }
	&:after {
		right: 0; }
	.slider-nav-prev,
	.slider-nav-next {
		position: absolute;
		top: 50%;
		margin-top: -17px;
		width: 14px;
		height: 34px;
		font-size: 0;
		z-index: 2;
		transition: opacity .2s ease-in-out;
		background-color: transparent;
		@include hover {
			opacity: 0.7; } }
	.slider-nav-prev {
		left: -22px;
		background: url(../img/slider-nav-prev.svg) no-repeat;
		background-size: contain; }
	.slider-nav-next {
		right: -22px;
		background: url(../img/slider-nav-next.svg) no-repeat;
		background-size: contain; } }
.slider-for {
	opacity: 0;
	@include sm {
		margin-top: 25px; }
	img {
		max-width: 100%; } }
.slider-nav {
	margin: 0 -5px;
	&__slide {
		padding: 0 5px; }
	img {
		height: 100px;
		width: 100%; } }
.slider-for__slide {
	@include sm {
		height: 300px !important; } }

.slider-nav-wrap {
	&:before,
	&:after {
		width: 0 !important; } }
